<template>
  <v-container class="my-5">
    <v-layout row wrap align-center justify-space-around class="lighten-2" flat>
      <span class="mr-3 "><v-icon>dashboard</v-icon> Toolbar</span>
      <v-flex md2>
        <v-btn color="success" small @click.stop="loadDialog"
          ><v-icon>add</v-icon> Add New</v-btn
        >
        <v-dialog
          max-width="600px"
          v-model="dialog"
          transition="fab-transition"
        >
          <v-card>
            <v-card-title>
              <h2>Add a New Product</h2>
            </v-card-title>
            <v-card-text>
              <v-form class="px-2" ref="form" enctype="multipart/form-data">
                <v-text-field
                  label="Name"
                  :rules="nameRules"
                  v-model="name"
                ></v-text-field>
                <v-textarea
                  label="Description"
                  :rules="descriptionRules"
                  v-model="description"
                ></v-textarea>
                <v-text-field
                  label="Quantity"
                  :rules="numberRules"
                  v-model="quantity"
                ></v-text-field>
                <v-select
                  :items="categoryItems"
                  :rules="[(v) => !!v || 'Category is required']"
                  v-model="categoryId"
                  label="Category"
                  :retain-focus="false"
                ></v-select>
                <v-switch v-model="status" label="Status"></v-switch>
                <!-- <input
				type="file"
				name="image"
				hidden
				ref="fileInput"
				accept="image/*"
				@change="onFilePicked"
				/> -->
                <v-file-input
                  label="Image"
                  accept="image/*"
                  color="primary"
                  placeholder="Pick an image"
                  prepend-icon="mdi-camera"
                  small
                  multiple
                  v-model="image"
                  @change="onPickFile"
                ></v-file-input>
                <v-flex md4 v-for="(image, index) in imageUrl" :key="index">
                  <img width="100px" :src="image" alt="" />
                </v-flex>
                <v-btn
                  color="success"
                  type="submit"
                  @click.prevent="doAddProduct"
                  :loading="loading"
                  >Add</v-btn
                >
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>

      <v-flex md2>
        <v-btn color="info" small
          ><v-icon>vertical_align_top</v-icon> Export Exel</v-btn
        >
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      name: "",
      description: "",
      quantity: "",
      image: null,
      categoryId: "",
      status: "",

      dialog: false,
      categoryItems: [],
      items: ["Active", "Unactive"],
      imageUrl: [],
      loading: false,
      numberRules: [
        (v) =>
          (!isNaN(parseFloat(v)) && v >= 0 && v <= 999) ||
          "Number has to be between 0 and 999",
        (v) => !!v || "Quantity is required",
        (v) =>
          (v && v.length <= 10) || "Quantity must be less than 10 characters",
      ],
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 25) || "Name must be less than 10 characters",
      ],
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length <= 100) ||
          "Description must be less than 10 characters",
      ],
    };
  },
  mounted() {
    this.getListCategory();
  },
  computed: {
    ...mapGetters("category", ["getCategory"]),
    ...mapState("product", ["products"]),
  },
  methods: {
    ...mapActions("category", ["getListCategory"]),
    ...mapActions("product", ["addProduct"]),
    async onPickFile() {
      let images = this.image;
      let arrImage = this.imageUrl;
      if (images.length > 0) {
        images.forEach((value) => {
          const fileReader = new FileReader();
          fileReader.readAsDataURL(value);
          fileReader.addEventListener("load", function() {
            arrImage.push(fileReader.result);
          });
        });
      } else {
        this.imageUrl = [];
      }
    },
    loadDialog() {
      this.dialog = true;
      this.getCategory.forEach((i) => {
        let dataSelect = {
          text: i.name,
          value: i.id,
        };
        this.categoryItems.push(dataSelect);
      });
    },
    doAddProduct() {
      if (this.$refs.form.validate()) {
        const formData = new FormData();
        let images = this.image;
        if (images) {
          for (let value of images) {
            formData.append("image", value);
          }
        }
        formData.append("name", this.name);
        formData.append("description", this.description);
        formData.append("quantity", this.quantity);
        formData.append("status", this.status);
        formData.append("categoryId", this.categoryId);
        const data = this.products.find((i) => {
          return i.name == this.name;
        });
        if (data) {
          let notify = {
            color: "error",
            text: "Product exits",
          };
          this.$emit("Notify", notify);
        } else {
          this.addProduct(formData);

          let notify = {
            color: "success",
            text: "Create Product Success",
          };
          this.$emit("Notify", notify);
          this.loading = true;
          this.$refs.form.reset();
          this.dialog = false;
        }
      }
      this.loading = false;
    },
  },
};
</script>

<style></style>
